import React, { useState } from "react";
import { AppBar, Toolbar, IconButton, Typography, Grid, Button } from '@mui/material';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LogoImage from './sie2.png'; 
import LogoutIcon from '@mui/icons-material/Logout';

const Header = () => {
    const [showLogout, setShowLogout] = useState(false);

    const handleAccountClick = () => {
      setShowLogout(!showLogout);
    };

    const handleLogout = () => {
      };
  return (
    <AppBar position="sticky" style={{ top: 0,background: 'linear-gradient(90deg, hsla(217, 100%, 50%, 1) 0%, hsla(186, 100%, 69%, 1) 100%)' }}>
      <Toolbar>
        <Grid container justifyContent="space-between" alignItems="center">
          <Grid item xs={6}>
            <Grid container alignItems="center" spacing={2}>
              <Grid item>
                <img src={LogoImage} alt="Logo" style={{ width: '40px', marginRight: '10px' }} />
              </Grid>
              <Grid item>
                <Typography variant="h6" component="div">
                Kab. Cirebon
                </Typography>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={6} sx={{ display: 'flex', justifyContent: 'right', alignItems: 'center' }}>
       
            <Typography variant="h6" component="div">
                  Admin
                </Typography>
                <IconButton color="inherit" onClick={handleAccountClick}>
              <AccountCircleIcon />
            </IconButton>
            {showLogout && (
                <Grid item>
                  <Button variant="contained" color="primary" endIcon={<LogoutIcon />} onClick={handleLogout} >
                    Logout
                  </Button>
                </Grid>
              )}
          </Grid>
        </Grid>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
